import InputField from './InputField';
import SelectField from './SelectField';
import TextAreaField from './TextAreaField';
import EmailField from './EmailField';
import DateField from './DateField';
import CheckboxField from './CheckboxField';
import ExtendedSelect from './ExtendedSelect';
import './styles/styles.scss';
var Form = /** @class */ (function () {
    function Form() {
        this.fieldArray = [];
        this.fieldArrayString = [];
        this.socket = new WebSocket("ws://localhost:8080");
    }
    Form.prototype.getValue = function () {
        var _this = this;
        this.fieldArray.forEach(function (element) {
            element.getValue();
            if (window.localStorage.getItem('data') == null) {
                _this.fieldArrayString.push(element.value);
            }
        });
        if (window.localStorage.getItem('data') == null) {
            window.localStorage.setItem('data', JSON.stringify(this.fieldArrayString));
        }
        document.getElementById('send').addEventListener('click', function () {
            var message;
            message = " ";
            //message=this.fieldArray.toString();
            _this.fieldArray.forEach(function (element) {
                message += element.value;
            });
            // console.log(this.fieldArray[0].value);
            console.log(message);
            _this.socket.send(message);
        });
    };
    Form.prototype.setValue = function () {
        this.fieldArray.forEach(function (element) {
            element.setValue();
        });
    };
    Form.prototype.render = function () {
        var inputField = new InputField("Imię");
        inputField.render();
        this.fieldArray.push(inputField);
        var textAreaField = new TextAreaField("Nazwisko");
        textAreaField.render();
        this.fieldArray.push(textAreaField);
        var dateField = new DateField("Data urodzenia");
        dateField.render();
        this.fieldArray.push(dateField);
        var emailField = new EmailField("email");
        emailField.render();
        this.fieldArray.push(emailField);
        var selectField = new SelectField("Wybrany kierunek studiów");
        selectField.render();
        this.fieldArray.push(selectField);
        var selectExtendedField = new ExtendedSelect("Wybrany kierunek studiów");
        selectExtendedField.render();
        this.fieldArray.push(selectExtendedField);
        var checkboxField = new CheckboxField("Czy preferujesz e-learning");
        checkboxField.render();
        this.fieldArray.push(checkboxField);
    };
    Form.prototype.sendMessage = function () {
        var that = this;
        this.socket.onopen = function (socket) {
            alert("Connected");
            that.socket.send("Hello!");
        };
        this.socket.onmessage = function (event) {
            alert('Message received: ' + event.data);
        };
    };
    Form.prototype.loadDate = function () {
        var loadItem = window.localStorage.getItem('data');
        var data = JSON.parse(loadItem);
        return data;
    };
    Form.prototype.putLoadDataToObject = function () {
        for (var index = 0; index < this.fieldArray.length; index++) {
            this.fieldArray[index].value = this.fieldArrayString[index];
        }
    };
    Form.prototype.createUI = function () {
        var node = document.createElement("LI");
        var button = document.createElement('button');
        button.innerText = "Stwórz tabele";
        button.onclick = function () { format.render(); };
        node.appendChild(button);
        var button2 = document.createElement('button');
        button2.innerText = "Zapisz";
        button2.onclick = function () { format.getValue(); };
        node.appendChild(button2);
        var button3 = document.createElement('button');
        button3.id = "send";
        button3.innerText = "send";
        button3.onclick = function () { format.sendMessage(); };
        node.appendChild(button3);
        document.getElementById("Main").appendChild(node);
    };
    return Form;
}());
var format = new Form();
