var BaseField = /** @class */ (function () {
    function BaseField() {
    }
    BaseField.prototype.createInput = function (name, type) {
        var node = document.createElement("LI");
        var textnode = document.createTextNode(name);
        var inputnode = document.createElement(type);
        inputnode.id = name;
        node.appendChild(textnode);
        node.appendChild(inputnode);
        document.getElementById("namelist").appendChild(node);
        return inputnode;
    };
    BaseField.prototype.clear = function (node, value) {
        value = "";
        node.remove(node);
        return value;
    };
    BaseField.prototype.edit = function (textnode, value, name) {
        value = document.getElementById(name).value;
        textnode.nodeValue = (name + ": " + value);
        return value;
    };
    return BaseField;
}());
export default BaseField;
